.main-animate a {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--light-bg);
  overflow: hidden;
}
.main-animate svg {
  width: 22px;
  height: 22px;
}
.main-animate svg {
  stroke: var(--chart-text-color);
}
.main-animate svg.fill-secondary {
  stroke: none;
}
.main-animate i {
  color: var(--chart-text-color);
  border-radius: 50%;
}

.pulse-wrapper a {
  border-radius: 50%;
}
.pulse-wrapper svg {
  animation: pulse-animate 1s infinite ease;
  border-radius: 50%;
}
.pulse-wrapper i {
  animation: pulse-animate 1s infinite ease;
}

.heartbeat-wrapper a {
  border-radius: 50%;
}
.heartbeat-wrapper svg {
  animation: heartbeat-rate 1s infinite ease;
  border-radius: 50%;
  stroke: #E44141;
}
.heartbeat-wrapper i {
  color: #E44141;
  animation: heartbeat-rate 1s infinite ease;
}

.wave-wrapper a {
  border-radius: 50%;
}
.wave-wrapper svg,
.wave-wrapper img {
  animation: water-wave 1s infinite ease;
  border-radius: 50%;
}
.wave-wrapper i {
  animation: water-wave 1s infinite ease;
}

.border-pulse-wrapper a {
  border: 4px double var(--recent-dashed-border);
  border-radius: unset;
}
.border-pulse-wrapper a svg,
.border-pulse-wrapper a i {
  border-radius: 1%;
}

.pulse-rhombus a {
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}
.pulse-rhombus svg,
.pulse-rhombus i {
  border-radius: 0;
}

.pulse-rabbet a {
  clip-path: polygon(0% 15%, 15% 15%, 15% 0%, 85% 0%, 85% 15%, 100% 15%, 100% 85%, 85% 85%, 85% 100%, 15% 100%, 15% 85%, 0% 85%);
}

@keyframes pulse-animate {
  0% {
    box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.1);
  }
  100% {
    box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
  }
}
@keyframes heartbeat-rate {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  15% {
    transform: scale(0.8);
  }
  20% {
    transform: scale(0.85);
    opacity: 1;
  }
  35% {
    transform: scale(1.3);
    opacity: 0.6;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(1);
  }
}
@keyframes water-wave {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}
