    transform: translateY(0);
  }
}
@keyframes right-eye {
  from {
    clip-path: polygon(0 75%, 100% 0, 100% 100%, 0 100%);
  }
  50%, to {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}
@keyframes left-eye {
  from {
