  }
  50%, to {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}
@keyframes mouth-lower {
  from {
    border-radius: 50% 50% 0 0/100% 100% 0 0;
    top: 1.75em;
    height: 0.75em;
    visibility: visible;
  }
  40% {
    border-radius: 50% 50% 0 0/100% 100% 0 0;
    top: 1.95em;
    height: 0.25em;
    visibility: visible;
  }
  50%, to {
    border-radius: 0;
    top: 2em;
    height: 0.125em;
    visibility: hidden;
  }
}
@keyframes mouth-upper {
  from, 50% {
    border-radius: 0;
    box-shadow: 0 -0.125em 0 inset;
    top: 2em;
    height: 0.125em;
    visibility: hidden;
  }
  62.5% {
    border-radius: 0 0 50% 50%/0 0 100% 100%;
    box-shadow: 0 -0.125em 0 inset;
    top: 1.95em;
    height: 0.25em;
    visibility: visible;
  }
  75% {
    border-radius: 0 0 50% 50%/0 0 100% 100%;
    box-shadow: 0 -0.125em 0 inset;
    top: 1.825em;
    height: 0.5em;
    visibility: visible;
  }
  to {
    border-radius: 0 0 50% 50%/0 0 100% 100%;
    box-shadow: 0 -0.8em 0 inset;
    top: 1.75em;
    height: 0.75em;
    visibility: visible;
  }
}
