/**=====================
  4.2 Theme-customizer CSS Start
==========================**/
.customizer-links {
  position: fixed;
  right: 0px;
  top: 50%;
  z-index: 3;
  transform: translate(0, -50%);
  box-shadow: 0 0 37px rgba(8, 21, 66, 0.1);
}
.customizer-links > .nav {
  border-radius: 8px;
  padding: 10px;
  background-color: #fff;
}
.customizer-links.open {
  right: 330px;
  border-radius: 8px 0 0 8px;
  transition: all 0.3s ease;
}
.customizer-links .nav-link {
  padding: 0;
  position: relative;
}
.customizer-links .nav-link div {
  padding: 4px;
}
.customizer-links .nav-link:before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(122, 112, 186, 0.1);
  border-radius: 5px;
  transform: scale(0);
  transition: all 0.3s ease;
}
.customizer-links .nav-link:after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: dashed;
  top: 12px;
  border-width: 5px 0 5px 5px;
  border-left-color: #cac6e3;
  opacity: 0;
  transform: scale(0);
  color: #fff;
  left: -10px;
  z-index: 2;
  transition: all 0.3s ease;
}
.customizer-links .nav-link:hover i {
  color: var(--theme-default);
}
.customizer-links .nav-link:hover:before, .customizer-links .nav-link:hover:after {
  opacity: 1;
  transform: scale(1);
  transition: all 0.3s ease;
}
.customizer-links .nav-link:hover span {
  opacity: 1;
  right: 38px;
  transform: scale(1);
  transition: all 0.3s ease;
}
.customizer-links .nav-link + .nav-link {
  margin-bottom: 5px;
}
.customizer-links .nav-link span {
  position: absolute;
  right: -60px;
  width: max-content;
  text-align: right;
  padding: 7px 10px;
  display: block;
  top: 0;
  border-radius: 5px;
  transform: scale(0);
  transition: all 0.3s ease;
  opacity: 0;
  color: #fff;
  background-color: #7A70BA;
  box-shadow: 0 0 37px rgba(8, 21, 66, 0.1);
}
.customizer-links .nav-link i {
  font-size: 17px;
  padding: 8px 10px;
  display: inline-block;
  cursor: pointer;
  margin: 0;
  color: gray;
  border-radius: 5px;
}

.customizer-contain {
  direction: ltr;
  position: fixed;
  height: 100vh;
  top: 79px;
  width: 333px;
  right: -335px;
  background-color: #fff;
  z-index: 8;
  transition: all 0.3s ease;
}
.customizer-contain.open {
  right: 0px;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px 1px rgba(68, 102, 242, 0.05);
}
.customizer-contain li {
  position: relative;
}
.customizer-contain li.active {
  box-shadow: 0 0 11px 5px rgba(226, 226, 226, 0.5);
}
.customizer-contain h5 {
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-top: 12px;
  padding-top: 12px;
  margin-bottom: 5px;
  color: #000;
}
.customizer-contain .customizer-header {
  padding: 15px 25px;
  border-bottom: 1px dashed rgba(106, 113, 133, 0.3);
}
.customizer-contain .customizer-header .icon-close {
  position: absolute;
  font-size: 18px;
  cursor: pointer;
  font-weight: 900;
  right: 24px;
  color: var(--theme-default);
}
.customizer-contain .customizer-header h5 {
  padding-top: 0;
  margin-top: 0;
}
.customizer-contain .customizer-body {
  padding: 15px 25px;
  max-height: calc(100vh - 200px);
  overflow-y: scroll;
}
